.ld-spin:after {
	position: absolute;
	margin: auto;
	width: 100%;
	height: 100%;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	content: " ";
	display: inline-block;
	background: center center no-repeat;
	background-size: cover;
	border-radius: 50%;
	border-style: solid;
	border-width: 0.15em;
	-webkit-background-clip: padding-box;
	border-color: currentColor currentColor currentColor transparent;
	box-sizing: border-box;
	}

