		}
	50% {
		-webkit-transform: rotate(180deg);
		transform: rotate(180deg);
		animation-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
		}
	100% {
		-webkit-transform: rotate(360deg);
		transform: rotate(360deg);
		}
	}
.btn-spinner {
	position: relative;
	-webkit-transition: all 0.3s;
	transition: all 0.3s;
	transition-timing-function: ease-in;
	overflow: hidden;
	}

.btn-spinner > .ld-spin {
	position: absolute;
	top: 50%;
	left: 50%;
	margin: -0.5em;
	opacity: 0;
	z-index: -100;
	-webkit-transition: all 0.3s;
	transition: all 0.3s;
	transition-timing-function: ease-in;
	}

.btn-spinner.btn-spinning > .ld-spin {
	opacity: 1;
	z-index: auto;
	}

.btn-spinner.btn-spinning.btn-spinner-pad {
	padding-right: 2.5em !important;
	}

.btn-spinner > .ld-spin {
	top: 50%;
	left: auto;
	right: 1em;
	}

